.tap-target {
	display: flex;
	flex-direction: column;
	justify-content: center;
	width: 12em;
	height: 12em;
	padding: 1em;
	margin: 0 auto;
	box-sizing: border-box;
  
	border-radius: 100%;
  
	font-size: 1.125em;
	text-align: center;
	color: #fff;
	background-color: indianred;
  
	cursor: pointer;
	touch-action: none;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
  
	transition: all 0.3s;
  }

.tap-hexagon {
	font-size: 1.125em;
	text-align: center;
	align-items: center;
	color: #fff;
	background-color: indianred;
  
	cursor: pointer;
	touch-action: none;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
  
	transition: all 0.5s;
  
}

.tap-hexagon.switch-bg {
	background-color: #f40;	
}

.tap-hexagon.large {
	transform: scale(1.25);
	transform: rotate(360deg);
  }
  
